
const user = {
	namespaced: true,
	state: {
		// 
		token: "",
		// 
		userInfo: {},
	},
	mutations: {
		// token
		setToken(state, token) {
			state.token = token
		},
		// 
		setUserInfo(state, userInfo) {
			state.userInfo = userInfo
		},
		// 
		clearAuth(state) {
			state.token = ""
			state.userInfo = {}
			uni.removeStorageSync("token")
			uni.removeStorageSync("userInfo")
		},
	},
}

export default user